import React from 'react';
import PropTypes from 'prop-types';
import ReactModal from '../modal';
import Spinner from '../spinner';
import './style.scss';

const UploadFileView = props => {
  const { 
    onChange,
    onClick,
    isLoading,
    isEmpty,
    fileInput,
    isOpen,
    onCloseModal
  } = props;

  if (isEmpty) {
    return (
      <div className='skeletonscreen-topbar'/>
    )
  }

  return (
    <>
      <button
        type='button'
        className='upload-photos'
        onClick={ onClick }
      >
        { isLoading ? <Spinner mode='dark' width='20px' height='20px' /> : null }
        <svg xmlns="http://www.w3.org/2000/svg" className='svg-icon' viewBox="0 0 384.97 384.97" width='15' height='15'>
          <path d="M372.939 264.641c-6.641 0-12.03 5.39-12.03 12.03v84.212H24.061v-84.212c0-6.641-5.39-12.03-12.03-12.03S0 270.031 0 276.671v96.242c0 6.641 5.39 12.03 12.03 12.03h360.909c6.641 0 12.03-5.39 12.03-12.03v-96.242c.001-6.652-5.389-12.03-12.03-12.03z"/>
          <path d="M117.067 103.507l63.46-62.558v235.71c0 6.641 5.438 12.03 12.151 12.03s12.151-5.39 12.151-12.03V40.95l63.46 62.558c4.74 4.704 12.439 4.704 17.179 0 4.74-4.704 4.752-12.319 0-17.011L201.268 3.5c-4.692-4.656-12.584-4.608-17.191 0L99.888 86.496a11.942 11.942 0 000 17.011c4.74 4.704 12.439 4.704 17.179 0z"/>
        </svg>
        <span className='upload-photos-label'>Upload file</span>
        <input 
          type="file" 
          multiple
          className="input-file"
          onChange={ onChange }
          ref={ fileInput }
        />
      </button>
      <ReactModal
        isOpen={ isOpen }
        onRequestClose={ onCloseModal }
        className="modal modal-error"
        overlayClassName="modal-mask"
      >
        <div className='modal-content'>
          <div className='modal-error-title'>Warning</div>
          <div className='error-text'>We could not get the geolocation data from your photos, you can add photos manually by selecting a country, and add photos yourself.</div>
        </div>
        <button 
          className='modal-btn'
          onClick={ onCloseModal }
        >
          OK
        </button>
      </ReactModal>
    </>
  )
}

UploadFileView.propTypes = {
  onChange: PropTypes.func,
  onClick: PropTypes.func,
  isLoading: PropTypes.bool
}

export default UploadFileView;